.categories {
  padding-top: 12px;
  margin-bottom: 85px;
  // .categories__container

  &__container {
  }

  // .categories__title

  &__title {
    padding-bottom: 40px;
  }

  // .categories__content

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    @media (min-width: 1460px) {
      grid-template-rows: 400px 400px;
    }
  }

  // .categories__item

  &__item {
  }
}
.block-category {
  position: relative;
  &:hover {
    color: #2a6788;
/*     &::before {
      width: 100%;
    } */
    .block-category__title_left {
      border-left: 3px solid #fd922e;
      background-color: #eef6f9;
/*       &::before {
        width: 100%;
      } */
    }
    .block-category__title_right {
      border-right: 3px solid #fd922e;
      background-color: #eef6f9;
/*       &::before {
        width: 100%;
      } */
    }
  }
  // .block-category_big

  &_big {
    grid-row-start: 1;
    grid-row-end: 3;
    transform: translate(-100%, 0);
  }

  &_second {
    transform: translate(0, -500%);
  }

  &_third {
    transform: translate(0, 100%);
  }

  // .block-category__image

  &__image {
    width: 100%;
    height: 100%;
    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .block-category__title

  &__title {
    font-family: "Poiret One";
    font-weight: 400;
    font-size: 48px;
    line-height: 116%;
    color: #82b5d1;
    position: absolute;
    top: 20px;
    z-index: 1;
    padding: 10px 30px;
    background-color: #eef6f900;
    transition: all 0.3s ease 0s;
    z-index: 10;
/*     &::before {
      content: "";
      position: absolute;
      top: 0;
      width: 0;
      height: 100%;
      background-color: #eef6f9;
      z-index: -1;
      transition: all 0.6s ease 0s;
    } */

    &_left {
      left: 0;
      border-left: 3px solid #fd922e00;
      border-radius: 0 10px 10px 0;
/*       &::before {
        border-radius: 0 10px 10px 0;
        left: 0;
      } */
    }

    &_right {
      right: 0;
      border-right: 3px solid #fd922e00;
      border-radius: 10px 0 0 10px;
/*       &::before {
        border-radius: 10px 0 0 10px;
        right: 0;
      } */
    }
  }
}
.from-left {
  animation: from-left 0.9s;
  animation-fill-mode: forwards;
  @keyframes from-left {
    0% {
      transform: translate(-100%, 0);
    }
    100% {
      transform: translate(0, 0);
    }
  }
}
.from-up {
  animation: from-up 0.9s;
  animation-fill-mode: forwards;
  @keyframes from-up {
    0% {
      transform: translate(0, -100%);
    }
    100% {
      transform: translate(0, 0);
    }
  }
}
.from-down {
  animation: from-down 0.9s;
  animation-fill-mode: forwards;
  @keyframes from-down {
    0% {
      transform: translate(0, 100%);
    }
    100% {
      transform: translate(0, 0);
    }
  }
}
.from-right {
  animation: from-right 0.9s;
  animation-fill-mode: forwards;
  @keyframes from-right {
    0% {
      transform: translate(200%, 0);
    }
    100% {
      transform: translate(0, 0);
    }
  }
}
